.pagesContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-image: url("../images/wallpaper.jpg");
  background-position: 80% 35%;
  height: 400px;
  width: 1100px;
  box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.3);
}

.pageItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 245px;
  height: 300px; /*320*/
  border: solid white;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 0.7);
  color: #474747;
  margin: 15px;
  overflow: hidden;
}

.pageItem h2 {
  padding: 4px;
}

.pageItem p {
  height: 120px;
  max-height: 100px; /* max-height: 124px; */
  max-width: 240px;
  font-size: 17px;
  /* text-overflow: ellipsis; */
  padding: 0 0 6px 0px;
  /* white-space: nowrap; */
  display: -webkit-box;
  -webkit-line-clamp: 5; /* -webkit-line-clamp: 6; */
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.pageItem img {
  width: 245px;
  height: 150px;
  margin-top: auto;
  /* flex-wrap: wrap; */
}
